@import url("/src/comp/Colors.css");
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap');

#AboutPage{
    background-color: var(--backGround);
}

#AboutSection-I img:hover{
    transform: scale(1.03);
    border-radius: 10px;
    transition: 0.3s;
}

#AboutSection-II img:hover{
    transform: scale(1.03);
    border-radius: 10px;
    transition: 0.3s;
}

#AboutPage img{
    transition: 0.3s;
}



/*----------------------------------------------*/
#AboutLanding{
    position: relative;
    text-align: center;
    margin-bottom: 10px;
}

#AboutLanding img{
    width: 100%;
    height: 80vh;
    object-fit: cover;
}

#AboutLanding h2{
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);

    
    font-family: 'Colus', serif;
    font-size: 12vh;
    font-weight: 600;
    backdrop-filter: blur(3px);
    color: white;
}
/*----------------------------------------------*/


#AboutSection-I{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: minmax(80vh, max-content);
    margin-bottom: 10px;
}

#AboutSection-I .imgDiv{
    grid-column: 2 / 4;
    grid-row: 1 / 2;
    width: 100%;
    height: 100%;
}

#AboutSection-I .textDiv{
    grid-column: 4 / 6;
    grid-row: 1 / 2;
    padding: 3vw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

#AboutSection-I .textDiv h2{
    font-family: 'Playfair Display', serif;
    font-size: 3rem;
}

#AboutSection-I .textDiv p{
    font-family: 'Playfair Display', serif;
    font-size: 1.5rem;
    text-align: left;
}

#AboutSection-I img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

/*----------------------------------------------*/

#AboutSection-II{
    display: grid;
    grid-template-columns: 1fr 1fr 2fr 1fr;
    grid-template-rows: minmax(80vh, max-content);
}

#AboutSection-II .imgDiv{
    grid-column: 3 / 4;
    grid-row: 1 / 2;
    
    display: flex;
    gap:5px
}

#AboutSection-II .textDiv{
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    min-width: 400px;
    padding: 3vw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;

}

#AboutSection-II img{
    width: 50%;
    height: 100%;
    object-fit: cover;
}

#AboutSection-II .textDiv h2{
    font-family: 'Playfair Display', serif;
    font-size: 3rem;
}

#AboutSection-II .textDiv p{
    font-family: 'Playfair Display', serif;
    font-size: 1.5rem;
    text-align: left;
}